<template lang="pug">
  .frame
    .mtoplist
      img.menu(src="./assets/menu.svg",@click="menuclick")
      transition(name="page",mode="out-in")
        .mlistAll(v-if="change",name="fade")
          div(@click="menuclick")
            router-link(to="/", class="mlist") 首頁
          div(@click="menuclick")
            router-link(to="/record", class="mlist") 簡歷 
          div(@click="menuclick")
            router-link(to="/self", class="mlist") 自傳 
          div(@click="menuclick")
            router-link(to="/design", class="mlist") 繪圖設計
          div(@click="menuclick")
            router-link(to="/entity", class="mlist") 實體作品
    .toplist
      router-link(to="/",class="list") 首頁
      router-link(to="/record",class="list") 簡歷 
      router-link(to="/self",class="list") 自傳
      router-link(to="/design",class="list") 繪圖設計
      router-link(to="/entity",class="list") 實體作品
    .bottomcontent
      transition(name="page",mode="out-in")
        router-view(:key="$router.path")
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      change: false
    }
  },
  methods:{
    menuclick(){
      this.change = !this.change
    }
  }
}
</script>

<style lang="sass">
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css)

*
  // border: 1px solid white
  font-family: "cwTeXYen", sans-serif

@mixin size($w,$h:$w)
  width: $w
  height: $h

@keyframs fade
  from
    opacity: 0
  to
    opacity: 1

.page-enter-active, .page-leave-active, .fade-enter-active, .fade-leave-active
  transition: 0.6s
.page-enter, .page-leave-to, .fade-enter, .fade-leave-to
  opacity: 0

html,body
  position: relative
  z-index: -1
  display: flex
  justify-content: center
  align-items: center
  margin: 0
  padding: 0
  +size(100%)
  background-color: #222

.frame
  +size(auto,100%)
  display: flex
  flex-direction: column

.bottomcontent
  position: relative
  z-index: 2
  display: flex
  // margin-top: 30px
  +size(100%,90%)

a
  text-decoration: none
  
.toplist
  position: relative
  z-index: 5
  height: 50px
  display: flex
  justify-content: center
  align-items: center
  margin: 15px
  .list
    color: rgba(white,0.8)
    +size(160px,40px)
    margin: 5px
    font-size: 24px
    display: flex
    justify-content: center
    align-items: center
    cursor: pointer
    &:hover
      border-bottom: 2px solid rgba(white,0.8)
    &:active
      border-bottom: none
.mtoplist
  display: none
  
//rwd網站設定-------------------------------
@media (max-width: 600px) 
  .toplist
    display: none
  .mtoplist
    display: block
    position: fixed
    left: 50%
    transform: translateX(-50%)
    margin: 0
    width: 100%
    height: 50px
    z-index: 50
    .menu
      position: absolute
      top: 20px
      right: 5%
      max-width: 30px
      cursor: pointer
    .mlistAll
      display: flex
      flex-direction: column
      top: 70px
      width: 90vw
      text-align: center
      margin: 0 auto
      // transition: 0.5s
      div
        height: 80px
        display: flex
        justify-content: center
        align-items: center
        transition: 0.5s
      .mlist
        color: rgba(black,0.7)
        font-size: 36px
        width: 70vw
        background-color: rgba(white,0.9)
        padding: 10px
        border-radius: 10px
</style>



